
import { defineComponent, ref, watch } from 'vue'
import {
    IonCol,
    IonRow,
    IonInput,
    modalController
} from "@ionic/vue"
import TouchField from "@/components/Forms/SIngleTouchField.vue"
import { Field } from "@/components/Forms/FieldInterface";
import { FieldType } from "@/components/Forms/BaseFormElements";

export default defineComponent({
    emits: [ 'onItemsPerPage', 'onSearchFilter' ],
    components: { 
        IonCol, 
        IonRow, 
        IonInput 
    },
    props: {
        totalRowCount: {
            type: Number,
            default: 15000
        },
        disableSearchFilter: {
            type: Boolean,
            default: false
        },
        disablePerPageFilter: {
            type: Boolean,
            default: false
        },
        showPerPageFilter: {
            type: Boolean,
            default: false
        }
    },
    setup(props, {emit}) {
        const itemsPerPage = ref(50)
        const searchFilter = ref('')

        async function launchKeyboard(currentField: Field, onFinish: Function) {
          const modal = await modalController.create({
            component: TouchField,
            backdropDismiss: false,
            cssClass: "full-modal",
            componentProps: {
              dismissType: 'modal',
              currentField,
              onFinish
            }
          });
          modal.present();
        }

        function launchSearcher() {
           launchKeyboard({
                id: 'search',
                helpText: 'Search table data',
                defaultValue: () => searchFilter.value,
                type: FieldType.TT_TEXT,
            }, 
            (data: any) => {
                searchFilter.value = data ? data.value : ''
                emit('onSearchFilter', searchFilter.value)
            })
        }

        watch(itemsPerPage, (newValue) => {
            if(typeof newValue === 'number') emit('onItemsPerPage', newValue)
        }, 
        { 
            immediate: true 
        })

        return {
            itemsPerPage,
            searchFilter,
            launchKeyboard,
            launchSearcher
        }
    },
})
