
import { defineComponent, PropType } from "vue";
import HisFooter from "@/components/HisDynamicNavFooter.vue";
import { toExportableFormat, ColumnInterface, RowInterface} from "@/components/DataViews/tables/ReportDataTable" 
import ReportTable from "@/components/DataViews/tables/ReportDataTable.vue"
import { IonCol, IonRow, IonPage,IonHeader, IonContent, IonToolbar, IonChip, IonFooter } from "@ionic/vue"
import { toCsv, toTablePDF } from "@/utils/Export"
import { Service } from "@/services/service"
import HisDate from "@/utils/Date"
import ReportFilter from "@/components/ReportFilter.vue"
import Pagination from "@/components/Pagination.vue"

export default defineComponent({
  components: {
    IonCol, 
    IonRow, 
    Pagination, 
    ReportTable, 
    ReportFilter, 
    HisFooter, 
    IonPage, 
    IonHeader,
    IonContent, 
    IonToolbar, 
    IonChip, 
    IonFooter 
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    columns: {
      type: Object as PropType<Array<ColumnInterface[]>>,
      required: true
    },
    rows: {
      type: Object as PropType<Array<RowInterface[]>>,
      default: () => []
    },
    rowParser: {
      type: Function
    },
    showFilters: {
      type: Boolean,
      default: false
    },
    rowsPerPage: {
      type: Number
    },
    asyncRows: {
      type: Function
    },
    paginated: {
      type: Boolean,
      default: false
    },
    customBtns: {
      type: Array,
      default: () => []
    },
    showReportStamp: {
      type: Boolean,
      default: true
    },
    footerColor: {
      type: String,
      default: 'dark'
    },
    customFileName: {
      type: String,
      default: ''
    },
    canExport: {
      type: Boolean,
      default: true
    },
    onFinish: {
      type: Function
    }
  },
  data: () => ({
    btns: [] as Array<any>,
    isTableLoading: false as boolean,
    searchFilter: '' as string,
    itemsPerPage: 50 as number,
    currentPage: 0 as number,
    tableRows: [] as any,
    totalPages: 0 as number,
    activeColumns: [] as any,
    activeRows: [] as any,
    date: HisDate.toStandardHisDisplayFormat(Service.getSessionDate()),
    apiVersion: Service.getApiVersion(),
  }),
  methods: {
    getFileName(){
      return this.customFileName ? this.customFileName : this.title
    }
  },
  created() {
    this.btns = [
      ...this.customBtns,
      {
        name: "CSV",
        size: "large",
        slot: "start",
        color: "primary",
        visible: this.canExport,
        onClick: () => {
          const {columns, rows} = toExportableFormat(this.activeColumns, this.activeRows)
          toCsv(columns, rows, this.getFileName())
        }
      },
      {
        name: "PDF",
        size: "large",
        slot: "start",
        color: "primary",
        visible: this.canExport,
        onClick: () => {
          const {columns, rows} = toExportableFormat(this.activeColumns, this.activeRows)
          toTablePDF(columns, rows, this.getFileName())
        }
      },
      {
        name: "Finish",
        size: "large",
        slot: "end",
        color: "success",
        visible: true,
        onClick: () => {
          if (typeof this.onFinish === 'function') {
            return this.onFinish()
          }
          this.$router.push({ path:'/' })
        }
      }
    ]
  }
})
